@import url(https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap);

.menu-icon{
    width: 3em;
    height: 3em;
}
.navbar-div{
   
    background-color: rgba(87, 68, 68, 0.9);
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    position: absolute;
    width: 85%;
    z-index: 5;
    height: 6em;
}
.navbar-div-big{
    /* background-color: #355E3B; */
    background-color:black;
    /* background-color: rgba(255,255,255,.9); */
    display: grid;
    grid-template-columns:repeat(9,0.5fr);
    align-content: center;
    justify-content: space-between;
    position: absolute;
    width: 95%;
    z-index: 5;
    height: 6em;
    border-radius: 5px;


    box-shadow: 1px 1px 25px #00ba51;
}
.navlink-big{
    text-decoration: none;
    color: white;
    display: grid;
    justify-content: center;
    align-content: center;
    /* font-family: 'Atkinson Hyperlegible', sans-serif; */
    font-family: 'Montserrat', sans-serif;
    font-weight:bold;
    font-size: 1rem;
}
.navbarlink{
    text-decoration: none;
    color: inherit;
    
}
.hcs-logo{
    display: grid;
    justify-content: center;
    align-content: center;
}
.logostyle{
    height: 4rem;
    width: 6rem;
}
.logostylebig{
    height: 4.5rem;
    width: 7rem;
}

.sample{
    border: 2px solid red;
    height: 100%;
}

.contact-btn{
    padding: 0.8em;
    background-color: #00ba51;
    border-radius: 25px;
    color: white;
    font-size: 0.9rem;
    font-family: 'Atkinson Hyperlegible', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media only screen and (max-width:1024px){
    .navbar-div{
        position: relative;
    }
}

@media only screen and (max-width:768px){
    
    .nav-parent-big{
     display: none;   
    }
    .nav-parent-small{
        width: 100%;
        display: flex;
        justify-content: center;
    }

}
@media only screen and (min-width:769px){
    .nav-parent-small{
        display: none;
    }
    .nav-parent-big{
        width: 100%;
        display: flex;
        justify-content: center;
    }
} 

.slider-wrap{
    /* filter: brightness(1.25); */
    position:sticky;
}

.carousel-content {
    width: 80%;
    position: absolute;
    bottom: 35%;
    left: 10%;
    z-index: 20;
    color: white;
    text-shadow: 0 1px 2px rgba(0,0,0,.6);
  }

  .animation-box {
    width: 100%;
    height: 20.5rem;
    /* height: fit-content; */
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    /* display: flex; */
    /* justify-content: left; */
}
.carousel-indicators{
    visibility: hidden;}
.halal-text{
  color: #134321;
}  
  @-webkit-keyframes topFadeOut {
    0% {
      position: absolute;
      top: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      top: 25%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }  
  @keyframes topFadeOut {
    0% {
      position: absolute;
      top: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      top: 25%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  @-webkit-keyframes bottomFadeOut {
    0% {
      position: absolute;
      bottom: -5rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      bottom: 25%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
    
  }
  
  @keyframes bottomFadeOut {
    0% {
      position: absolute;
      bottom: -5rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      bottom: 25%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
    
  }
  
  @-webkit-keyframes topFadeOutVertical {
    0% {
      position: absolute;
      top: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      top: 45%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  @keyframes topFadeOutVertical {
    0% {
      position: absolute;
      top: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      top: 45%;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  @-webkit-keyframes rightFadeInOut {
    0% {
      position: absolute;
      right: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      right: 10rem;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
      right: 10rem;
    }
  }
  
  @keyframes rightFadeInOut {
    0% {
      position: absolute;
      right: -3rem;
      opacity: 0;
    }
  
    75% {
      position: absolute;
      right: 10rem;
      opacity: 1;
    }
  
    100% {
      opacity: 1;
      right: 10rem;
    }
  }
  
  @-webkit-keyframes fadeInOut {
    0% {
      opacity: 0;
    }
  
    45% {
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  @keyframes fadeInOut {
    0% {
      opacity: 0;
    }
  
    45% {
      opacity: 1;
    }
  
    100% {
      opacity: 1;
    }
  }
  
  .first-text {
    font-size: 4.4rem;
    position: absolute;
    left: 4rem;
    top: 5rem;
    opacity: 1;
    -webkit-animation-name: topFadeOut;
            animation-name: topFadeOut;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    /* color: #134321; */
    color: white;
    /* background-color: rgba(255, 255, 255, 0.5); */
    /* color: white; */
    -webkit-filter: brightness(1.75);
            filter: brightness(1.75);
    font-family: 'Raleway', sans-serif;

    text-shadow: -0.5px 0.5px 1px black,
    0.5px 0.5px 1px black,
    1px -1px 0 black,
    -0.5px -0.5px 0 black;

  }
  .caption{
    font-size: 1rem;
    margin-left: 0.5em;
    color: white;
    /* color: #134321; */
    font-family: 'Raleway', sans-serif;
}
